            <span>
              筛选后总数 ：
              <span class="changeColor">{{lastList}}</span>
            </span>
          </p>
        </div>
        <div class="conBot">
          <h3>名单分配</h3>
          <div class="titleWrap">
            <p>可分总数: {{canIncloudNum}}</p>
            <div style="margin-left:100px">
              <span>要分总数:</span>
              <input v-model="divideNum" style="width:70px;height:30px;padding-left:18px" type="text" />
              <el-button
                type="primary"
                style="width:60px;height:34px;margin-top:3px;"
                @click="divideNumEv"
              >均分</el-button>
            </div>
            <p style="margin-left:100px">
              已分总数：
              <span>{{alradCloudNum}}</span>
            </p>
          </div>
          <div class="selectList" v-for="item in downData" :key="item.lable">
            <div class="first listFr">
              <el-checkbox  v-if="item.classId == 2 || item.classId == 3 || item.tsrCode == ''" disabled v-model="item.checked"></el-checkbox>
              <el-checkbox v-else v-model="item.checked" @change="inputGetNum"></el-checkbox>
              <span class="spanList"  v-if="item.step == '4' ">{{item.departAbbr}} -{{item.tsrCode}}</span>
              <span class="spanList" v-else>{{item.departAbbr}}</span>
              <i v-if="item.departList && item.departList.length > 0">
                <img @click="changeFalse(item)" v-if="!item.way" src="../../assets/image/up.png" />
                <img @click="changeTrue(item)" v-else src="../../assets/image/down.png" />
              </i>
              <el-input
                 v-if="item.classId == 2 || item.classId == 3 || item.tsrCode == ''"
                disabled
                class="treeInput"
                v-model="item.inputNum"
                placeholder
              ></el-input>
              <el-input v-else class="treeInput" v-model="item.inputNum" placeholder @blur="inputGetNum"></el-input>
              <!--  第二级 -->
              <div
                class="second listFr"
                v-if="item.way && item.departList && item.departList.length > 0"
              >
                <div  class="listFr" v-for="seitem in item.departList" :key="seitem.lable">
                  <el-checkbox   v-if="seitem.classId == 2 || seitem.classId == 3 || seitem.tsrCode == ''" disabled v-model="seitem.checked"></el-checkbox>
                  <el-checkbox v-else v-model="seitem.checked"  @change="inputGetNum"></el-checkbox>
                  <span class="spanList" v-if="seitem.step == '4' ">{{seitem.departAbbr}} -{{seitem.tsrCode}}</span>
                  <span class="spanList" v-else>{{seitem.departAbbr}}</span>
                  <i v-if="seitem.departList && seitem.departList.length > 0">
                    <img
                      @click="changeSeFalse(seitem)"
                      v-if="!seitem.way"
                      src="../../assets/image/up.png"
                    />
                    <img @click="changeSeTrue(seitem)" v-else src="../../assets/image/down.png" />
                  </i>
                  <el-input
                    class="treeInput"
                    v-if="seitem.classId == 2 || seitem.classId == 3 || seitem.tsrCode == ''"
                    disabled
                    v-model="seitem.inputNum"
                    placeholder
                  ></el-input>
                  <el-input class="treeInput" v-else v-model="seitem.inputNum" placeholder  @blur="inputGetNum"></el-input>

                  <!--  第三级 -->
                  <div
                    class="threed listFr"
                    v-if="seitem.way &&   seitem.departList && seitem.departList.length > 0"
                  >
                    <div  class="listFr" v-for="thitem in seitem.departList" :key="thitem.lable">
                      <el-checkbox v-if="thitem.tsrCode == ''" disabled v-model="thitem.checked"></el-checkbox>
                      <el-checkbox v-else v-model="thitem.checked"  @change="inputGetNum"></el-checkbox>
                      <span class="spanList" v-if="thitem.step == '4'">{{thitem.departAbbr}} -{{thitem.tsrCode}}</span>
                      <span class="spanList" v-else>{{thitem.departAbbr}}</span>
                      <i v-if="thitem.departList && thitem.departList.length > 0">
                        <img
                          @click="changeSeFalse(thitem)"
                          v-if="!thitem.way"
                          src="../../assets/image/up.png"
                        />
                        <img @click="changeSeTrue(thitem)" v-else src="../../assets/image/down.png" />
                      </i>
                      <el-input
                        v-if="thitem.tsrCode == ''"
                        disabled
                        class="treeInput"
                        v-model="thitem.inputNum"
                        placeholder
                      ></el-input>
                      <el-input v-else class="treeInput" v-model="thitem.inputNum" placeholder  @blur="inputGetNum"></el-input>
                      <!-- 第四级 -->
                      <div class="fourd listFr" v-if="thitem.way && thitem.departList && thitem.departList.length > 0"
                      >
                        <div class="listFr" v-for="fouritem in thitem.departList" :key="fouritem.lable">
                          <el-checkbox  v-if="fouritem.tsrCode == ''" disabled  v-model="fouritem.checked" ></el-checkbox>
                          <el-checkbox v-else v-model="fouritem.checked"  @change="inputGetNum"></el-checkbox>
                          <span class="spanList" v-if="fouritem.step == '4'">{{fouritem.departAbbr}} -{{fouritem.tsrCode}}</span>
                          <span class="spanList" v-else>{{fouritem.departAbbr}}</span>
                          <i v-if="fouritem.departList && fouritem.departList.length > 0">
                            <img  @click="changeSeFalse(fouritem)"   v-if="!fouritem.way"  src="../../assets/image/up.png" />
                            <img  @click="changeSeTrue(fouritem)" v-else src="../../assets/image/down.png" />
                          </i>
                          <el-input
                            v-if="fouritem.tsrCode == ''"
                            disabled
                            class="treeInput"
                            v-model="fouritem.inputNum"
                            placeholder
                          ></el-input>
                          <el-input
                            v-else
                            class="treeInput"
                            v-model="fouritem.inputNum"
                            placeholder
                             @blur="inputGetNum"
                          ></el-input>

                          <!-- 第五级 -->
                          <div class="fiftiy listFr" v-if="fouritem.way && fouritem.departList && fouritem.departList.length > 0"
                          >
                            <div  class="listFr" v-for="fiftyitem in fouritem.departList" :key="fiftyitem.lable">
                              <el-checkbox v-if="fiftyitem.tsrCode == ''"  disabled  v-model="fiftyitem.checked" ></el-checkbox>
                              <el-checkbox v-else v-model="fiftyitem.checked"  @change="inputGetNum"></el-checkbox>
                              <span class="spanList" v-if="fiftyitem.step == '4'">{{fiftyitem.departAbbr}} - {{fiftyitem.tsrCode}}</span>
                              <span class="spanList" v-else>{{fiftyitem.departAbbr}}</span>
                              <el-input
                                v-if="fiftyitem.tsrCode == ''"
                                disabled
                                class="treeInput"
                                v-model="fiftyitem.inputNum"
                                placeholder
                              ></el-input>
                              <el-input
                                v-else
                                class="treeInput"
                                v-model="fiftyitem.inputNum"
                                placeholder
                                 @blur="inputGetNum"
                              ></el-input>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <!--  </el-scrollbar> -->
          <div class="bottomBtn">
            <el-button type="primary" @click="sureBtn">确定</el-button>
            <el-button type="primary" plain  @click="successDialogClosed()">返回</el-button>
          </div>
        </div>
      </div>
    </div>
    <div class="listLower">
         <el-dialog
        class="dialogDi firstFlag"
        :visible.sync="firstDialogVisibleFlag"
        width="30%"
      >
        <img src="../../assets/image/messages.png" />
        <span
          class="successSpan"
        >本次计划分配{{wanIncloudNum}}条名单,请确认是否执行分配</span>
        <span class="sucessBtn">
           <el-button type="primary" @click="firstDialogVisibleFlag = false">取消</el-button>
          <el-button type="primary" @click="firstFlag()">确定</el-button>
        </span>
      </el-dialog>
      <el-dialog
        class="dialogDiv"
        :visible.sync="dialogVisibleFlag"
        width="30%"
      >
        <img style="margin-bottom:20px " src="../../assets/image/messages.png" />
        <span v-if="dialog1">只能对平行机构进行平分</span>
        <span v-if="dialog2">平分机构不能为空</span>
        <span v-if="dialog3">名单数量不可均分，请修改要分总数</span>
        <span v-if="dialog4">已分总数不可大于可分总数</span>
        <span v-if="dialog5">单次下发数量不可超过800，请修改名单数量</span>
      
      
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="dialogClose()">确 定</el-button>
        </span>
      </el-dialog>

      <el-dialog
        class="dialogDiv succes"
        :visible.sync="successDialogVisibleFlag"
        width="30%"
      >
        <img src="../../assets/image/sucessPic.png" />
        <span
          class="successSpan"
        >本次计划下发{{totalCount}}条名单,{{cgNum}}条下发成功,{{gsNum}}条已有归属坐席,{{ctNum}}条已归属电销转介绍</span>
        <span class="sucessBtn">
          <el-button type="primary" @click="successDialogClosed()">我知道啦</el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</template>

<script scpped>
import Vue from "vue";
import {
  getListIssued,
  getCustomerList,
  getAllProvince,
  getListIssuedSum,
  queryAllPartDataList,
  mdxf
} from "@/api/info";
export default {
  name: "questionBank",
  data() {
    return {
      divide: 0, //可分人数的数目综合
      divideNum: 0, //要均分的数目
      canIncloudNum:'',//可分总数
      totalCount: "", //计划下发数量
      cgNum: "", //成功下发
      ctNum: "", //归属电销转介绍
      gsNum:'',//已有归属坐席数量
      alradCloudNum:0,//已分总数
      waySe: true, //二级箭头
      way: true, //一级箭头
      isActive: true,
      orgId: "",
      checked: false,
      allProvince: [], //省份数组
      allCitys: [], //市数组
      allclassify: [], //区数组
      livingProvince: "", //省份
      livingArea: "", //市
      livingCity: "", //区
      realName: "",
      ageStart: "",
      ageEnd: "",
      genger: "",
      allProvincValue: "", //省value
      municipalValue: "", //市value
      batchIdList: "", //列表数组
      tsrCode: "",
      orgId:'',
      totalList: "",
      lastList: 0,
      downData: [],
      sureList: {},
      nodeList: {},
      dialogVisibleFlag: false,
      dialog1: false,
      dialog2: false,
      dialog3: false,
      dialog4: false,
      dialog5: false,
      dialog6: false,
      currentDialog: 1,
      gengerList: [
        {
          lable: "女",
          value: "2"
        },
        {
          lable: "男",
          value: "1"
        }
      ],
      user: {}, //当前用户
      successDialogVisibleFlag: false, //成功后的弹框
      departCode:'',
      position:'',
      firstDialogVisibleFlag:false,
      wanIncloudNum:'',//想要下发
    };
  },
  created() {
    let map = this.$route.query.mapBatchId;
    this.batchIdList = map;
    let userStorage = JSON.parse(sessionStorage.getItem("userInfo"));
    this.user = userStorage;
    this.tsrCode = userStorage.tsrCode;
    this.orgId = userStorage.orgId;
    this.departCode = userStorage.departCode;
    this.position = userStorage.position
  },
  mounted() {
    //查询总数
    this.getListIssuedSum();
    //查询部门信息
    this.queryAllPartDataInfo();
  },
  methods: {
    //获取总数
    getListIssuedSum(type) {
      let param = {
        realName: this.realName,
        ageStart: this.ageStart,
        ageEnd: this.ageEnd,
        genger: this.genger,
        orgId: this.orgId,
        livingProvince: this.allProvincValue,
        batchIdList: this.batchIdList,
        livingArea: this.livingCity, //区
        livingCity: this.municipalValue //市
      };
      getListIssuedSum(param).then(res => {
        let data = res && res.listIssuedsum;
        if (type) {
          this.lastList = data;
        } else {
          this.totalList = data;
        }
        this.canIncloudNum = this.lastList ? this.lastList : this.totalList
      }).catch(err =>{
       this.$message.error('操作失败');
     });
    },
    //获取部门信息
    queryAllPartDataInfo() {
      //客户orgId；
      let param = {
        parentId: this.departCode,
        position: this.position,
      };
      queryAllPartDataList(param).then(res => {
        let data = res && res.dataList;
        this.downData = data;
      }).catch(err =>{
       this.$message.error('操作失败');
     });
    },

    //均分按钮
     divideNumEv(){
                // 总分
                let _divideNum = this.divideNum;
                let _downData = this.downData;
                if(parseInt(this.canIncloudNum) < parseInt(this.divideNum)) {
                   this.dialogVisibleFlag = true;
                   this.dialog4 = true;
                   this.divideNum = 0;
                   return false
                }

                //第一层非数组 从第二层开始 记录每层选中数量
                let departListCheckedNum = new Map();
                let _current = 0;
                // 遍历本层选中数量
                this.getCheckedNumByItem(_current,departListCheckedNum,_downData);
               // console.log('departListCheckedNum = ', departListCheckedNum);
                // 校验每层选中数量 每次只能平分一层 取平均数
                let inputNum = this.validChecked(departListCheckedNum,_divideNum);
               // console.log('inputNum = ', inputNum);
                // 递归遍历，将平均数更新进去
                if(inputNum){
                    this.putInputNum(_downData, inputNum);
                }
            },
            putInputNum(departList, inputNum) {
                for (let depart of departList) {
                    if (depart.checked) {
                        depart.inputNum = inputNum;
                    }
                    if (Array.isArray(depart.departList)&& depart.departList.length>0) {
                        this.putInputNum(depart.departList, inputNum);
                    }
                }
            },
            validChecked(departListCheckedNum,_divideNum) {
                //  选中数量
                let checkedNum = 0;
                let isChecked = false;
                for (let val of departListCheckedNum.values()) {
                    if (val > 0) {
                        if (isChecked) {
                            this.dialogVisibleFlag = true;
                            this.dialog1 = true;
                            // console.log('不是同一级别机构')
                            return false;
                        }
                        isChecked = true;
                        checkedNum = val;
                    }
                }
                if (checkedNum === 0) {
                    this.dialogVisibleFlag = true;
                    this.dialog2 = true;
                    // console.log('机构为空')
                    return false;
                }
                if (_divideNum % checkedNum != 0) {
                    this.dialogVisibleFlag = true;
                    this.dialog3 = true;
                   // console.log('只能均分')
                    return false;
                }
               // console.log(_divideNum , checkedNum)
                return _divideNum / checkedNum ;
            },
            getCheckedNumByItem(_current,departListCheckedNum,departList) {
                // 当前层级选中数量
                let _crrentChecked = 0;
                for (let depart of departList) {
                    if (depart.checked === true) {
                        _crrentChecked++;
                    }
                    if (Array.isArray(depart.departList)&& depart.departList.length>0) {
                        this.getCheckedNumByItem(_current + 1, departListCheckedNum, depart.departList);
                    }
                }
                if (departListCheckedNum !=null && departListCheckedNum.get(_current) != null && departListCheckedNum.get(_current) != 0) {
                    departListCheckedNum.set(_current,departListCheckedNum.get(_current)+_crrentChecked);
                }else {
                    departListCheckedNum.set(_current,_crrentChecked);
                }
            },
    fliterList(){
      let init = [];
      this.getCheckedInput(this.downData,init);
      return init
    },
    //过滤已经选择和填写的数字
    getCheckedInput(departList , init){
        departList.forEach(item => {
          if (item.inputNum != '') {
            let obj = {};
                obj.step = item.step; 
                obj.orgId = item.departId;
                obj.num = item.inputNum;
                obj.orgName = item.departAbbr;
                obj.tsrCode = item.tsrCode;
                init.push(obj);
          }
          if(Array.isArray(item.departList) && item.departList.length > 0){
              this.getCheckedInput(item.departList, init);
          }
        });
        return init;
    },
    inputGetNum(){
      this.getTotalNum()
    },
    
    getTotalNum(){
      let list =  this.fliterList();
      let totalNum = 0; // 分发的总数
      if(list){
          list.forEach(item =>{
              totalNum += parseInt(item.num) ;
          })
          //判断分发总数是否大于800
          if(totalNum >= 800){
              this.dialogVisibleFlag = true;
              this.dialog5 = true;
              return false
          }
          this.alradCloudNum = totalNum
         // this.divideNum = totalNum;
      }
      if(this.alradCloudNum > this.canIncloudNum || this.divideNum > this.canIncloudNum){
        this.dialogVisibleFlag = true;
        this.dialog4 = true;
        return false;
      }
      return list;
    },
    //确定按钮
    sureBtn() { 
        let list =  this.getTotalNum();
        let wanIncloudNum = 0;
        if(list){
           list.forEach(item =>{
              wanIncloudNum += parseInt(item.num) ;
            })
            this.wanIncloudNum = wanIncloudNum;
              this.firstDialogVisibleFlag = true;
        }
    
    },
    firstFlag(){
        let list =  this.getTotalNum();
        //console.log('list',list)
        //提交过滤后的数组
        if(list){
          this.submitDate(list);
        }
    },
   
    //判断省数组是否有数据
    allProvinceEvent(type) {
      this.getAllProvince(type);
    },
    //获取省,市,区份
    getAllProvince(type, organId) {
      let param = { type: type, organId: organId };
      getAllProvince(param).then(res => {
        let data = res && res.dataList;
        if (type == 1) {
          this.allProvince = data;
         // console.log("省", this.allProvince);
        } else if (type == 2) {
          this.allCitys = data;
        } else {
          this.allclassify = data;
        }
      }).catch(err =>{
       this.$message.error('操作失败');
     });
    },
    //获取省的value
    changeProvincValue() {
     // console.log(this.allProvincValue);
    },
    //市查询
    municipal() {
      this.getAllProvince(2, this.allProvincValue);
    },
    //区查询
    classify() {
      this.getAllProvince(3, this.municipalValue);
    },
    //弹框
    handleClose(done) {
      this.$confirm("确认关闭？")
        .then(_ => {
          done();
        })
        .catch(_ => {});
    },

    //提交数据
    submitDate(list) {
      let mdParam = {
        realName: this.realName,
        ageStart: this.ageStart,
        ageEnd: this.ageEnd,
        genger: this.genger,
        orgId: this.orgId,
        livingProvince: this.allProvincValue,
        batchIdList: this.batchIdList,
        livingArea: this.livingCity, //区
        livingCity: this.municipalValue //市
      }
      let param = {
        tsrCodeList: list,
        batchIdList: this.batchIdList,
        user: this.user,
        mdParam:mdParam
      };
       mdxf(param).then(res =>{
          if(res.code == '000'){
              this.successDialogVisibleFlag = true;
              this.totalCount = res.totalCount;
              this.cgNum = res.cgNum;
              this.ctNum = res.ctNum;
              this.gsNum = res.gsNum;
          }else{
           // alert('提交失败')
          }
      }).catch( err =>{
       // alert('提交失败')
      })
    },
    successDialogClosed() {
      this.$router.push({ path: "/listAllocation" });
      //location.replace(document.referrer);
    },
    //关闭弹框
    dialogClose() {
      this.dialog1 = false;
      this.dialog2 = false;
      this.dialog3 = false;
      this.dialog4 = false;
      this.dialog5 = false;
      this.dialogVisibleFlag = false;
    },
    //向下箭头点击
    changeFalse(item) {
      item.way = true;
    },
    changeTrue(item) {
      item.way = false;
    },
    //二级箭头
    changeSeFalse(item) {
      item.way = true;
    },
    changeSeTrue(item) {
      item.way = false;
      Vue.set(this.downData);
    }
  } //methods
};
</script>

